





































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import wx from 'weixin-js-sdk'
import { formatDate } from '@/until/index'

@Component({
  components: {
    HelloWorld
  },
  filters: {
     getFormatDate (date: any) {
      return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号'
    },
    getRepairType (type: any) {
      if (type === 'type1') {
        return '开锁服务'
      } else if (type === 'type2') {
        return '公共报修'
      } else if (type === 'type3') {
        return '个人报修'
      } else {
        return '其他报修'
      }
    }
  }
})
export default class Home extends Vue {
  $createDialog : any
  $createToast : any
  // 声明周期钩子
  model = { details: '', type: 'type1' }
  fields = [
             { 
              type: 'textarea',
              modelKey: 'details',
              props: { placeholder: '请描述您要报修的详情...', maxlength: '100', autoExpand: true, autofocus: true },
              rules: { required: true },
              messages: {
                required: '请输入报修内容'
              }
            }, 
            { 
              type: 'select',
              modelKey: 'type',
              label: '报修类型',
              props: { 
                placeholder: '请输入',
                type: 'text',
                options: [
                  {
                    text: '开锁服务',
                    value: 'type1'
                  },
                  {
                    text: '公共报修',
                    value: 'type2'
                  },
                  {
                    text: '个人报修',
                    value: 'type3'
                  }
                ]
             }
            }
          ]

  houseId = ''
  // tab切换配置
  selectedLabelDefault = 'repair'
  tabBarList = [{
    label: '快捷报修',
    value: 'repair'
  }, {
    label: '报修记录',
    value: 'repairList'
  }]

  // 
  page = 1
  isShowMore = true
  repairList = []

  activated () {
    // this.getWeiXinConfig()
    this.pageInit()
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
    console.log(result)
    console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex)
  }
  getBindingInfo () {
    let query = this.$route.query
    if (query.code) {
      const toast = this['$createToast']({
        time: 0,
        txt: '数据获取中...'
      })
      toast.show()
      this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
        if (response.success) {
          if (response.data.openid) {
            this.openid = response.data.openid
            this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
          } else if (response.data.sessionId) {
            this.$store.commit('SET_SESSION_ID', response.data.sessionId)
            this.$store.commit('SET_USER_INFO', response.data.userInfo)
            this.getHouseList()
          }
        }
        toast.hide()
      })
    } else {
      console.log('不是微信登陆')
    }
  }
  pageInit () {
    let query = this.$route.query
    if (query.houseId) {
      this.houseId = query.houseId
    }
    if (this.$store.state.sessionId && !this.houseId) {
      this.getHouseList()
    } else if (query.code && !this.$store.state.sessionId) {
      this.getBindingInfo()
    }
  }

  getHouseList () {
    this.$store.dispatch('getHouseList', {}).then((response) => {
      if (response.data.list) {
        this.listData = response.data.list
        if (!this.houseId) {
          this.houseId = this.listData[0].id
        }
        this.$store.commit('SET_HOUSE_LIST', this.listData)
      }
    })
  }

  tabBarChange (value:any) {
    if (value === 'repairList') {
      this.page = 1
      this.repairList = []
      this.getRepairList()
    }
  }
  showMoreClick () {
    this.page++
    this.getBillListByHouseId()
  }

  submitHandler (e: any) {
     e.preventDefault()
     let houseInfo = this.getHouseInfo()
     let query = {}
     query.accountId = houseInfo.accountId
     query.address = houseInfo.text
     query.houseId = houseInfo.id
     query.floorId = houseInfo.floorId
     query.type = this.model.type
     query.details = this.model.details
     this.$store.dispatch('addRepair', query).then((response) => {
      if (response.success) {
         this.model = { details: '', type: 'type1' }
         this.selectedLabelDefault = 'repairList'
         this.tabBarChange('repairList')
         this['$createDialog']({
            type: 'alert',
            content: '报修成功, 随后会有工作人员联系您！',
            icon: 'cubeic-ok'
          }).show()
      }
    })
  }

  get houseOptions () {
    this.$store.state.houseList.forEach((obj) =>{
      obj.value = obj.id
      obj.text = obj.account_info[0].name + ' ' + obj.build_info[0].name + ' '+ obj.unitNum + '单元' + obj.floorNum + '层' + obj.roomNum + '室'
    })
    return this.$store.state.houseList
  }
  get userInfo () {
    return this.$store.state.userInfo
  }

  getHouseInfo () {
    let info = {}
    this.$store.state.houseList.forEach((obj) =>{
      if (obj.id === this.houseId) {
        info = obj
      }
    })
    return info
  }

  getRepairList () {
    const toast = this['$createToast']({
      time: 0,
      txt: '数据获取中...'
    })
    toast.show()
    this.$store.dispatch('getRepairList', { page: this.page, houseId: this.houseId }).then((response) => {
      if (response.success) {
          if (response.data.list.length > 0) {
            this.repairList = this.repairList.concat(response.data.list)
          } else {
            this.isShowMore = false
          }
      }
      toast.hide()
    })
  }

  showMoreClick () {
    this.page++
    this.getRepairList()
  }
}
